import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';
import axios from 'axios';

import Home from './panels/Home';
import AddInfo from './panels/AddInfo';
import Album from './panels/Album';
import Ranking from './panels/Ranking';
const App = () => {
	const [activePanel, setActivePanel] = useState('home');
	const [fetchedUser, setUser] = useState(null);
	const [user_acces_token, setUserToken] = useState(null);
	const [popout, setPopout] = useState(<ScreenSpinner size='large' />);

	useEffect(() => {
		bridge.subscribe(({ detail: { type, data } }) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		async function fetchData() {
			const user = await bridge.send('VKWebAppGetUserInfo');
			const userToken = await bridge.send("VKWebAppGetAuthToken", { "app_id": 7384847, "scope": "friends,status,photos" });
			setUser(user);
			setUserToken(userToken);
			setPopout(null);
		}
		fetchData();
	}, []);

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};

	const share = per => {

		const count_to_post = per.currentTarget.dataset.count;
				
			console.log(user_acces_token.access_token);
			bridge.send("VKWebAppShowWallPostBox", { "message": per.currentTarget.dataset.mes, "attachments": per.currentTarget.dataset.img })
			.then(res => {

				const data = new FormData()
				data.append('user_id', fetchedUser.id)
				data.append('hero_count', count_to_post)
				data.append('post_id', res.post_id)
				data.append('first_name', fetchedUser.first_name)
				data.append('last_name', fetchedUser.last_name)
				data.append('photo_200', fetchedUser.photo_200)
				data.append('user_acces_token', user_acces_token.access_token)
				axios.post("https://skynets.space/wall", data, {
					// receive two    parameter endpoint url ,form data
				})
					.then(res => { // then print response status
						console.log(res.data)
					})
			})
	};

	return (
		<View activePanel={activePanel} popout={popout}>
			<Home id='home' fetchedUser={fetchedUser} go={go} />
			<Album id='album' fetchedUser={fetchedUser} share={share} go={go} />
			<AddInfo id='AddInfo' fetchedUser={fetchedUser} go={go} />
			<Ranking id='Ranking' fetchedUser={fetchedUser} go={go} />
		</View>
	);
}

export default App;

