import React, { useState, useEffect } from 'react';
//import axios from 'axios';
import PropTypes from 'prop-types';
import { platform, IOS, Input } from '@vkontakte/vkui';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import bridge from '@vkontakte/vk-bridge';
import axios from 'axios-jsonp-pro';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import logo from '../img/logo.png';

const osName = platform();


function Rank(props) {
    const [Ranks, setHeroes] = useState([]);

    function isEmpty(str, zapaska) {
        if (str == null)
            return zapaska;

        return str;
    }

    function getUser() {


        //setHeroes(<ScreenSpinner size='large' />);
        let url = 'https://skynets.space/wall';
        axios.get(url)
            .then(function (response) {
                // handle success	
                console.log(response);
                setHeroes(response.data);

            })
            .catch(function (error) {
                // handle error
                console.log(error);
            })
            .finally(function () {
                // always executed
            });
    }

    function getInfoRepost(item) {
        return item.user_id + " " + item.count;
    }

    function getUserInfo(item) {

        let url = 'https://api.vk.com/method/users.get?user_ids=' + item.user_id + '&fields=bdate,photo_100&access_token=' + item.acces_token + '&v=5.103';

        axios.jsonp(url)
            .then(function (response) {
                // handle success	
                console.log(response)

            })
            .catch(function (error) {
                // handle error
                console.log(error);
            })
    }

    return (
        <div>

            {getUser()}

            <div class="flex">
                {Ranks.map(item => (

                    //<div class="item" data-key={item.user_id}>

                    //     <p>{getInfoRepost(item)}</p>

                    //   </div>

                    <Group style={{width:'100%'}} title="User Data Fetched with VK Bridge">
                        <Cell 
                            before={item.photo_200 ? <Avatar src={item.photo_200} /> : null}
                            description={'Репостов сделано: ' + item.count + "\n\r Героичность фамилии:" + item.hero_count}
                        >
                            <p>{`${item.first_name} ${item.last_name}`}</p>
                        </Cell>
                    </Group>

                ))}
            </div>
            <div class="logo">
				<img src={logo} />
			</div>
        </div>
    );
}

const Ranking = props => (

    <Panel id={props.id}>
        <PanelHeader
            left={<PanelHeaderButton onClick={props.go} data-to="home">
                {osName === IOS ? <Icon28ChevronBack /> : <Icon24Back />}
            </PanelHeaderButton>}
        >
            Рейтинг пользователей
		</PanelHeader>

        < Rank
            last_name={props.fetchedUser.last_name}
            share={props.share}
        />
    </Panel>

);

Ranking.propTypes = {
    id: PropTypes.string.isRequired,
    go: PropTypes.func.isRequired,
    share: PropTypes.func,
};

export default Ranking;
